<template>
  <div>
      <!-- index 下的二级路由出口 -->
      <transition enter-active-class="animate__animated animate__fadeIn">
        <router-view></router-view>
      </transition>

      <!-- 底部导航 -->
      <footer class="footer">
        <!-- active-class 激活的样式 -->
        <router-link to="/index/home" active-class="select">首页</router-link>
        <router-link to="/index/order"  active-class="select">订单</router-link>
        <router-link to="/index/mine" active-class="select">我的</router-link>
      </footer>
  </div>
</template>

<script>
export default {

}
</script>

<style scoped>
.footer{
  display: flex;
  width: 100vw;
  height: .8rem;
  background: #ccc;
  position: fixed;
  left: 0;
  bottom: 0;

}
.footer a{
  flex: 1;
  text-align:center;
  line-height: .8rem;
}
.footer .select{
  background: #ff4400;
  color: #fff;
}
</style>



